body {
  /*padding-top: 50px;*/
  padding: 0;
  margin: 0; }

html, body, #root {
  height: 100%; }

.sidebar {
  background: #212121;
  color: #fff; }

.CardHeaderText span {
  color: #fff; }

.ListText h3 {
  color: #fff; }

.dashboard__login {
  padding-top: 200px;
  text-align: center; }

/*helper class*/
.text-center {
  text-align: center; }

.register {
  width: 900px;
  margin: 50px auto; }
  .register input {
    display: block; }

.dashboard {
  font-family: 'Roboto'; }

.tab-route {
  list-style: none;
  text-align: center; }
  .tab-route li {
    display: inline-block;
    padding: 10px 20px; }
    .tab-route li a {
      text-decoration: none;
      color: #212121;
      text-transform: uppercase; }
      .tab-route li a.active {
        color: #ff4081; }

.pagination {
  position: relative; }
  .pagination__step {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: inline-block;
    width: 100px;
    text-align: center;
    height: 18px;
    margin: auto; }



